
//menu wrapper
.menu-list {
    width: 224px;

    .menu-item {
        margin-left: 0;
    }

    align-items: start;
}

.menu-popper {
    padding-bottom: 8px;
}

.menu-body {
    overflow-y: auto;
   
}

.menu-item {
    display: block;
    width: 100%;
    text-align: left;
    line-height: 1.8rem;
    font-size: 1.4rem;
    font-weight: 500;
    padding: 11px 16px;

    &.separate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}


.header {
    position: relative;
    height: 50px;
    margin-top: -8px;
    flex-shrink: 0;
}

.back-btn {
    width: 50px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
}

.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    font-weight: 600;
    transform: translate(-50%, -50%);
}

